header {
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #00806f;
  nav {
    display: flex;

    li {
      display: flex;
    }

    li:nth-child(n + 2) {
      margin-left: 15px;
    }
    a {
      text-decoration: none;
      color: #ffff;
      font-size: 21px;
      font-weight: 600;
    }
    a:hover {
      transform: translateY(-1px);
    }
  }
}
.active_link {
  text-decoration-line: underline;
  text-underline-offset: 2px;
}
