.formCardContainer {
  display: flex;
  flex-direction: row;
  margin-top: 20px;
  overflow-y: auto;
  max-width: 1200px;
  flex-wrap: wrap;
  gap: 10px;
}

.formCard {
  display: flex;
  border-radius: 8px;
  background-color: rgb(71, 202, 141);
  color: #313131;
  font-size: 18px;
  font-weight: 500;
  padding: 10px;

  .imageContainer {
    display: flex;
    width: 150px;

    img {
      object-fit: contain;
      width: 100%;
    }
  }

  .infoContainer {
    display: flex;
    margin-left: 10px;
    flex-direction: column;
    align-items: flex-start;
  }
}
