.searchContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40%;
  margin-top: 30px;
  border: 2px solid #979797;
  padding: 0 5px;
  border-radius: 15px;

  input {
    display: flex;
    width: 100%;
    font-size: 30px;
    border: none;
    border-radius: 15px;
    padding: 0 5px;
    outline: none;
    color: #4d4d4d;
  }
}

.btnSearch {
  display: flex;
  align-items: center;
  justify-content: center;
}
