main {
  display: flex;
  justify-content: space-evenly;

  .loadingImg {
    display: flex;
    align-self: center;
  }
}
.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.cardsField {
  margin-top: 30px;
  //max-width: 1600px;
  column-count: 4;
  column-gap: 10px;
  row-gap: 10px;
  line-height: 0;
}
@media (max-width: 1850px) {
  .cardsField {
    column-count: 3;
  }
}
@media (max-width: 1410px) {
  .cardsField {
    column-count: 2;
  }
}
@media (max-width: 1000px) {
  .cardsField {
    column-count: 1;
  }
}

.btnControl {
  display: flex;
  align-items: center;
  position: sticky;
  top: 20vh;
  right: 20px;
  height: 60px;
  user-select: none;
  .btnPage {
    width: 40px;
    border: 1px solid #ec6666;
    transition: all 0.4s;
    cursor: pointer;
  }
  .btnPage:hover {
    color: #fff;
    background-color: #ec6666;
  }

  .prev {
    border-radius: 123px 0 0 123px;
    margin-right: 5px;
  }
  .next {
    border-radius: 0 123px 123px 0;
    margin-left: 5px;
  }
  span {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
  .statusBar {
    display: flex;
    flex-direction: column;
    height: 100%;
    span {
      padding: 0 3px;
    }
    .page {
      border-bottom: 1px solid #a1a1a1;
    }
  }
}
