.card {
  position: relative;
  box-shadow: 1px 1px 6px 1px rgba(34, 60, 80, 0.3);
  margin-bottom: 10px;
  transition: all 0.5s;

  img {
    height: auto;
    width: 100%;
  }
}
.card:hover {
  transform: scale(1.01);
}
.hiddenDiv {
  position: absolute;
  display: flex;
  bottom: 0;
  width: 100%;
  height: 100%;
}

.btnPlay {
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  align-self: center;
  margin-left: 75px;
  width: 100px;
  height: 100px;
  transition: all 0.3s;
  cursor: pointer;
}

.btnPlay:hover {
  transform: scale(1.02);
}

.cardContent {
  display: flex;
  align-self: flex-end;
  align-items: flex-start;
  padding: 5px 10px;
  width: 100%;
  height: fit-content;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  line-height: normal;

  .text {
    display: flex;
    text-align: start;
    max-width: 80%;
    margin-bottom: 20px;
  }
  .footerContent {
    display: flex;
    width: 100%;
    justify-content: space-between;
    font-style: italic;
  }
}
