.notFound {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #444444;
  width: 100%;
  height: calc(100vh - 80px);
  span {
    font-weight: bold;
  }
  span:nth-child(1) {
    font-size: 180px;
  }
  span:nth-child(2) {
    font-size: 40px;
  }
  li {
    list-style: none;
    margin-top: 15px;
    a {
      color: inherit;
      font-size: 17px;
    }
  }
}
